.start {
    display: block;
    text-align: center;
    margin: 5% auto;
}

.start h1 {
    font-size: 8vw;
}

@media screen and (min-width: 1000px) {
    .start h1 {
       font-size: 80px;
    }
}

@media screen and (max-width: 300px) {
    .start h1 {
       font-size: 24px;
    }
}

.start button {
    background-color: rgb(102, 252, 241);
    padding: 10px 25px;
    border-radius: 20px;
    border: 0;
    font-size: 4vw;
    font-weight: bold;
    margin-bottom: 6vw;
}

@media screen and (min-width: 700px) {
    .start button {
       font-size: 28px;
       margin-bottom: 42px;
    }
}

@media screen and (max-width: 400px) {
    .start button {
       font-size: 16px;
       margin-bottom: 42px;
    }
}

.start button:hover {
    background-color: rgb(11, 12, 16);
    padding: 8px 23px;
    border: 2px solid rgb(102, 252, 241);
    color: rgb(102, 252, 241);
    font-weight: bold;
}

#rsm {
    width: 20%;
}

#imperial {
    width: 40%;
}

@media (min-width:700px) {
    #imperial {
        width: 280px
    }
    #rsm {
        width: 140px
    }
}

@media (max-width:400px) {
    #imperial {
        width: 160px
    }
    #rsm {
        width: 80px
    }
}

#sponsors {
    display: flex;
    align-items: baseline;
    justify-content: space-around;
}

#sponsors img {
    margin: 20px auto;
}

@media (max-width:600px) {
    #sponsors {
        flex-direction: column;
    }
}

@media (min-width:700px) {
    #sponsors {
        display: inline-block;
    }
    #sponsors img {
        margin: 20px 70px;
    }
}

